import emailjs from 'emailjs-com';
import { useState } from 'react';

const Contact = () => {
    const [buttonText, setbuttonText] = useState('Send');

    function handleSubmit(e) {
        e.preventDefault();
        setbuttonText('Sending...');

        emailjs.sendForm('service_q13fovt', 'template_jql9yko', e.target, 'user_efqfa17Bx8wsMHokEsfbK')
        .then(() => {
            setbuttonText('Sent');
            setTimeout( () => {
                setbuttonText('Send another');
            }, 2000);
        }, () => {
            setbuttonText('Failed')
            setTimeout( () => {
                setbuttonText('Send again');
            }, 2000);
        });

        e.target.reset();
    }

    return (
        <section className="outer-layer about">
            <div className="inner-layer">
                <div className="section-name">
                    <hr />
                    <h2>./About</h2>
                </div>
                <div className="about--contact">
                    <form action="" className="contact--form" onSubmit={handleSubmit}>
                        <div className="input-field">
                            <span className="input-name">Name</span>
                            <input type="text" name="username" id="username" required />
                        </div>
                        <div className="input-field">
                            <span className="input-name">Email</span>
                            <input type="email" name="email" id="email" required />
                        </div>
                        <div className="input-field">
                            <span className="input-name">Message</span>
                            <textarea name="message" id="message" required></textarea>
                        </div>
                        <button type="submit" className="btn">{buttonText}</button>
                    </form>
                    <img className="contact--image" src="./images/contact-image.png" alt="Fancy Illustration"></img>
                </div>

            </div>
        </section>
    )
}

export default Contact;