import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Work = () => {
    return (
        <section className="outer-layer work">
            <div className="inner-layer">
                <div className="section-name">
                    <hr />
                    <h2>./Work</h2>
                </div>
                <div className="work--project__grid">
                    <div className="work--project">
                        <img src="./images/project-bcc.png" alt="Project Display" className="work--project-img" />
                        <div className="project--details">
                            <h3>Bomiriya Central College</h3>
                            <span className="details--vert-line" />
                            <div className="details--info">
                                <p>B.C.C. is a school in SL.
                                    The Design and Development was all done by myself. This site includes all the information about the school and a teachers only login system which prompts them to
                                    change the content of the webpage without any coding.
                                </p>

                                <div className="details--info__bottom">
                                    <div className="info__bottom--icons">
                                        <a href="https://github.com/ShadeyGhost">
                                            <FontAwesomeIcon icon="link" />
                                        </a>
                                        <a href="https://github.com/ShadeyGhost">
                                            <FontAwesomeIcon icon={['fab', 'github']} />
                                        </a>
                                    </div>
                                    <span className="info__bottom--time-passed">
                                        <FontAwesomeIcon icon="history" />
                                        4 months ago
                                    </span>
                                </div>
                            </div>
                        </div>

                    </div>

                    <div className="work--project">
                        <img src="./images/project-pupsDen.png" alt="Project Display" className="work--project-img" />
                        <div className="project--details">
                            <h3>The Puppy Den</h3>
                            <span className="details--vert-line" />
                            <div className="details--info">
                                <p>Puppy den is a place for people to post ads about pets. This is an ongoing project I'm currently
                                     working on. Once this project is done it will be available to use in Sri Lanka. 
                                </p>

                                <div className="details--info__bottom">
                                    <div className="info__bottom--icons">
                                        <a href="https://github.com/ShadeyGhost">
                                            <FontAwesomeIcon icon="link" />
                                        </a>
                                        <a href="https://github.com/ShadeyGhost">
                                            <FontAwesomeIcon icon={['fab', 'github']} />
                                        </a>
                                    </div>
                                    <span className="info__bottom--time-passed">
                                        <FontAwesomeIcon icon="history" />
                                        1 month ago
                                    </span>
                                </div>
                            </div>
                        </div>

                    </div>

                    
                </div>
            </div>
        </section>
    )
}

export default Work;